import React, { Component } from 'react';
import './App.css';
import Scheme from './components/scheme0/Scheme';
import FiltersContainer from './components/FiltersContainer/FiltersContainer';
import Footer from './components/Footer/Footer';
import * as data from './db.json';
import CompanyInfoModal from './components/CompanyInfoModal/CompanyInfoModal';
import { CSSTransition } from 'react-transition-group';
var _= require('lodash');

class App extends Component {
  constructor() {
    super();
    this.state = { 
      selected: [],
      categoriesFilter: [],
      brandsFilter: [],
      current: {},
      modalIsOpen: false,
      currentLevel: '0',
      levels: [
        { name: '0', countSelected: null }, 
        { name: '1', countSelected: null }, 
        { name: '2', countSelected: null }
      ]
    };
  }

  onChangeCategoriesFilter = (value) => {
    this.setState({ categoriesFilter: value }, () => this.search());
  }

  onChangeBrandsFilter = (value) => {
    this.setState({ brandsFilter: value }, () => this.search());
  }

  onChangeLevelsFilter = (value) => {
    this.setState({ currentLevel: value });
  }

  search() {
    const { companies } = data;
    const { brandsFilter, categoriesFilter } = this.state;
    let selected, levels = [0, 0, 0];
    if (categoriesFilter.length === 0 && brandsFilter.length === 0) {
    this.setState({ 
      selected, 
      levels: [
        { name: '0' }, 
        { name: '1' }, 
        { name: '2' }
      ] 
    });
    return false;
    }
    selected = _.filter(companies, (company) => {
      if (categoriesFilter.length === 0 && brandsFilter.length === 0) {
        return false;
      }
      let catFr = false;
      for (let i in company.subcategories) {
        if (_.find(categoriesFilter, { value: company.subcategories[i] })) {
          catFr = true;
          break;
        }
      }
      catFr = Boolean(categoriesFilter.length === 0 || catFr);

      let brandFr = false;
      for (let i in company.brands) {
        if (_.find(brandsFilter, { value: company.brands[i] })) {
          brandFr = true;
          break;
        }
      }
      brandFr = Boolean(brandsFilter.length === 0 || brandFr);

      return catFr && brandFr;
    });
    selected = selected.map(company => {
      levels[company.level]++;
      return `shop-${company.level}-${company.place}`;
    })
    this.setState({ 
      selected: selected,
      levels: [
        { name: '0', countSelected: levels[0] }, 
        { name: '1', countSelected: levels[1] }, 
        { name: '2', countSelected: levels[2] }
      ]
    });

  }

  selectCurrentCompany = (e) => {
    console.log(e.target.id);
    const current = e.target.id.split(/-/);

    let currentCompany = _.find(data.companies, { level: current[1], place: current[2] });
    if (!currentCompany) {
      return;
    }
    let subcategoriesLabels = [];
    for (let i in currentCompany.subcategories) {
      let subcategory = _.find(data.subcategories, { id: currentCompany.subcategories[i]});
      subcategoriesLabels.push(subcategory)
    }
    currentCompany.subcategoriesLabels = subcategoriesLabels;
    this.setState({ currentCompany: currentCompany, modalIsOpen: true });
  }

  closeModal = (e) => {
    e.stopPropagation();
    this.setState({ modalIsOpen: false });
    setTimeout(() => {
    this.setState({ currentCompany: null });
    }, 400);
  }

  render() {
    console.log(this.state.selected);
    const { brands, categories, subcategories } = data;
    const { currentCompany, selected, levels, currentLevel, modalIsOpen} = this.state;
    return (
      <div className="App">
        <main>
          <h1>Схема комплекса</h1>
          <FiltersContainer
            categories={categories} 
            subcategories={subcategories}
            onChangeCategoriesFilter={this.onChangeCategoriesFilter}
            brands={brands}
            onChangeBrandsFilter={this.onChangeBrandsFilter}
            levels={levels}
            onChangeLevelsFilter={this.onChangeLevelsFilter}
            currentLevel={currentLevel}
          />
          <Scheme 
            selected={selected}
            currentCompany={currentCompany}
            selectCurrentCompany={this.selectCurrentCompany}
            currentLevel={currentLevel}
          >
            { 
              // modalIsOpen &&
              <CSSTransition 
                in={modalIsOpen} 
                timeout={1000} 
                classNames="fade"
              >
                <CompanyInfoModal 
                  company={currentCompany} 
                  isOpen={modalIsOpen} 
                  closeModal={this.closeModal }
                />
              </CSSTransition>
            }
          </Scheme>
        </main>
        <Footer/>
      </div>
    );
  }
}

export default App;
