import React, { Component } from 'react';
import './companyInfoModal.css';


class CompanyInfoModal extends Component {
  render() {
    const { company, closeModal } = this.props;
    if (!company) {
      return (<div></div>)
    }
    return (
      <div className="companyInfoModal">
        <div class="content">
          <button class="close-btn" onClick={closeModal}>
            <img src={require('../../assets/icons/close-cross.svg')} alt="close-btn"></img>
          </button>
          <h2 className="copmanyInfoModalHeader">{company.name}</h2>
          {
            company.image && require(`../../assets/img/${company.name}.png`) &&
            <div className="imageContainer">
              <img src={require(`../../assets/img/${company.name}.png`)} alt={`${company.name} logo`} ></img>
            </div>
          }
          {
            company && company.place 
            && <p>Этаж {company.level}, место {company.place.toUpperCase()}</p>}
          {
            company.phone &&
            <React.Fragment>
              <p className="attribute-descr">Телефон: </p>              
              <p>{company.phone}</p>
            </React.Fragment>
          }
          {
            company.phone2 && 
            <p>{company.phone2}</p>
          }
          {
            company.subcategoriesLabels &&
            <p className="attribute-descr">Ассортимент:</p>
          }
          {
            company.subcategoriesLabels && 
            company.subcategoriesLabels.map((label, ind) => 
            <p key={ind}>{label.name}</p>
            )
          }
        </div>
      </div>
    );
  }
}

export default CompanyInfoModal;
