import React, { Component } from 'react';
var classNames = require('classnames');

class LevelsFilter extends Component {
  render() {
    const { onChange, options, currentLevel } = this.props;
    console.log(currentLevel === options[0].name);
    return (
      <React.Fragment>
        <div class="levelsFilterHeader">
          <p>Этаж № <br/>
          {options[0].countSelected > 0 && <span>(Найдено)</span>}
          </p>        
        </div>
        { options.map((level, ind) => (
          <button 
            key={ind} 
            onClick={() => onChange(level.name)} 
            className={classNames({'selectedLevel': level.name === currentLevel})}
          >
            {level.name}
            {level.countSelected > 0 && `(${level.countSelected})`}
          </button>
        ))
        }
      </React.Fragment>
    );
  }
}

export default LevelsFilter;
 