import React, { Component } from 'react';
import BrandsFilter from '../brandsFilter/BrandsFilter';
import CategoriesFilter from '../categoriesFilter/CategoriesFilter';
import './filtersContainer.css';
import FilterIcon from '../../assets/icons/filter.svg';
import LevelsFilter from '../LevelsFilter/LevelsFilter';

class FitersContainer extends Component {
  customStyles = {
    valueContainer: (provided, state) => ({
      ...provided,
      textOverflow: "ellipsis",
      maxWidth: "90%",
      whiteSpace: "nowrap",
      overflow: "hidden",
      display: "initial"
    })
  };
  render() {
    const { 
      onChangeCategoriesFilter, 
      onChangeBrandsFilter, 
      onChangeLevelsFilter,
      categories, 
      subcategories, 
      brands,
      levels, 
      currentLevel
    } = this.props;
    console.log(levels);
    return (
      <div class="filtersContainer">
        <div class="categoriesFilter">
          <div class="filterHeader">
            <div class="filterIcon">
              <img src={FilterIcon} alt="filter icon"/>
            </div>
            Фильтр по ассортименту
          </div>
          <CategoriesFilter
            categories={categories}
            subcategories={subcategories}
            onChange={onChangeCategoriesFilter}
            styles={this.customStyles}
          />

        </div>
        <div class="brandsFilter">
          <div class="filterHeader">
            <div class="filterIcon">
              <img src={FilterIcon} alt="filte icon"/>
            </div>
            Бренды  
          </div>
          <BrandsFilter
            class="brandsFilter"
            brands={brands}
            onChange={onChangeBrandsFilter}
          />
        </div>
        <div class="levelsFilter">
          <LevelsFilter 
            options={levels}
            onChange={onChangeLevelsFilter}
            currentLevel={currentLevel}
          />
        </div>        
      </div>
    );
  }
}

export default FitersContainer;
