import React from 'react';
import './scheme.css';
import Scheme0 from './Scheme0';
import Scheme1 from './Scheme1';
import Scheme2 from './Scheme2';

export default class Scheme extends React.Component {
  constructor() {
    super();
    this.state = {
      mouseIsDown: false,
      translateTransform: '0, 0',
      prevMousePosition: { x: 0, y: 0 }
    };
  }

  getMousePosition(e) {
    var CTM = e.target && e.target.getScreenCTM();
    return {
      x: (e.clientX - CTM.e) / CTM.a,
      y: (e.clientY - CTM.f) / CTM.d
    };
  }

  onMouseMove = (e) => {
    if (this.state.mouseIsDown) {
      const dragX = e.clientX;
      const dragY = e.clientY;
      const prevX = this.state.prevMousePosition.x;
      const prevY = this.state.prevMousePosition.y;
      // const coord = this.getMousePosition(e);
      // this.setState({ translateTransform: `${coord.x}, ${coord.y}` });
      this.setState({ 
        translateTransform: `${dragX - prevX}, ${dragY - prevY}`,
        prevMousePosition: { x: dragX, y: dragY } 
    });
      console.log(dragX, dragY);
      // console.log(coord);
    }
  }

  startDrag = (e) => {
    this.setState({ 
      mouseIsDown: true,
      prevMousePosition: {
        x: e.clientX,
        y: e.clientY
      } 
    });
    // e.offsetX = e.clientX;
    // e.offsetY = e.clientY;
  }

  render() {
    const { selected, selectCurrentCompany, children, currentCompany, currentLevel } = this.props;
    const current = currentCompany && `shop-${currentCompany.level}-${currentCompany.place}`;
    return (
      <div class="scheme">
        {children}
        {/* <div
         onMouseDown={this.startDrag}
         onMouseUp={() => this.setState({ mouseIsDown: false })}
         onMouseLeave={() => { this.setState({ mouseIsDown: false })}}
         onMouseMove={this.onMouseMove}
        > */}
          {
            currentLevel === '0' && 
            <Scheme0 {...this.props} 
              translateTransform={this.state.translateTransform}
            />
            }
          {currentLevel === '1' && <Scheme1 {...this.props} />}
          {currentLevel === '2' && <Scheme2 {...this.props} />}

        {/* </div> */}

      </div>
    );
  }
}